@use 'base/variables' as v;
@use 'base/mixins' as m;

.horas{
    @include m.reset-list;

    @include m.grid (1,2rem);

    @include m.tablet {
        @include m.grid (2,2rem);
    }

    &__hora{
        color: v.$azul;
        border: 1px solid v.$azul;
        border-radius: .5rem;
        padding: 1rem 2rem;
        text-align: center;
        transition-duration: 200ms;

        &:hover{
            cursor: pointer;
            color: v.$blanco;
            background-color: v.$azul;
            scale: 1.1;
        }

        &--seleccionada{
            @extend .horas__hora;
            background-color: v.$azul;
            color: v.$blanco;
        }

        &--desabilitada{
            @extend .horas__hora;
            border: 1px solid v.$gris;
            color: v.$gris;
        
            &:hover{
                background-color: v.$blanco;
                color: v.$gris;
                scale: 1;
                cursor: not-allowed;
            }
        }
    }
}