@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{
    display: flex;
    flex-direction: column;

    @include m.desktop {
        flex-direction: row;
    }

    &__enlace{
        color: v.$blanco;
        text-transform: uppercase;
        font-weight: v.$black;
        font-size: 2.2rem;
        padding: 2rem;
        text-align: center ;
    
        &--actual,
        &:hover{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}