@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra{
    background-color: v.$azul;
    
    &__contenido{
        @include m.contenedor;
        display: flex;
        flex-direction: column;
        
        @include m.desktop {
            justify-content: space-between;
            flex-direction: row;
            align-items: center;
        }
    }

    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
    }
}