@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$gris-claro;

    &__header{
        background-color: v.$gris-oscuro;
        padding: 2rem;
    }

    &__header-grid{
        display: flex;
        flex-direction: column;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center
        }
    }

    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
    }

    &__form{
        text-align: center
    }

    &__submit--logout{
        @include m.enlace;
    }

    &__grid{
        display: flex;
        flex: 1;
        overflow-y: auto;
    }

    &__sidebar{
        width: 12rem;
        background-color: lighten($color: v.$gris-oscuro, $amount: 10);
    }

    &__menu{
        margin: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    &__enlace{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1rem;
        gap: 1rem;
        color: lighten($color: v.$gris-oscuro, $amount: 40);
        transition: 300ms background-color ease-in-out, color ease, border-radius ease;

        &:hover,
        &--actual
        {
            background-color: lighten($color: v.$gris-oscuro, $amount: 20);
            color: v.$blanco;
            border-radius: .5rem;
        }
        
    }

    &__icono{
        font-size: 2rem;
    }

    &__enlace-texto{
        font-size: 1.1rem;
        font-weight: v.$bold;
        text-transform: uppercase;
    }

    &__contenido{
        padding: 1rem 1rem 3rem 1rem;
        width: 100%;
        overflow-y: scroll;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__contenedor-boton{
        @include m.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 5rem;
        }
    }

    &__boton{
        @include m.boton (v.$azul,v.$blanco);
        text-transform: uppercase;
        font-size: 1.4rem;
        @include m.tablet {
            width: auto; 
        }
    }

    &__contenedor{
        @include m.shadow;
        background-color: v.$blanco;
        border-radius: 1rem;
        padding: 2rem;    
    }

    &__formulario{
        @extend .dashboard__contenedor;
        @include m.contenedor-md;
    }

    &__grafica{
        @include m.contenedor-sm;
    }

}