@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones{
        display: flex;
        justify-content: space-between;
        margin: 2rem 0;
    
    &--centrar{
        @extend .acciones;
        justify-content: center;
    }
    
    &__enlace{

        display: block;
        font-size: 1.4rem;
        font-weight: v.$delgada;
        margin: 2rem 0;

        &:last-of-type{
            margin-bottom: 0;
        }

        @include m.tablet {
            display: flex;
        }
    }
}