@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-ponentes{
    @include m.reset-list;
    @include m.grid (1,1rem);

    &__ponente{
        color: v.$azul;
        border: 1px solid v.$azul;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;

        &:hover{
            cursor: pointer;
        }

        &--seleccionado{
            @extend .listado-ponentes__ponente;
            background-color: v.$azul;
            color: v.$blanco;
            
        }
    }

}