@use 'base/variables' as v;

.alerta{
    padding: 1.5rem 1.5rem 1.5rem 3rem;
    margin: 1rem 0;
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: v.$bold;

    &__exito{
        background-color: rgba($color: v.$verde, $alpha: 0.3);
        border-left: v.$verde solid .5rem;
        color: v.$verde;
    }

    &__error{
        background-color: lighten($color: v.$rojo, $amount: 60);
        border-left: v.$rojo solid .5rem;
        color: v.$rojo;
    }


}