@use 'base/variables' as v;
@use 'base/mixins' as m;

.paquetes{

    &__grid{
        @include m.contenedor;
        @include m.grid (1,2rem);
        align-items: flex-start;

        @include m.tablet {
            @include m.grid (3, 3rem);
        }
    }

    &__submit{
        @include m.boton (v.$azul,v.$blanco);
        margin-top: 3rem;
    }
}

.paquete{
    @include m.card;
    &:nth-child(2){
        order: -1;
        @include m.tablet {
            order: unset;   
        }
    }


    &__lista{
        @include m.reset-list;
        line-height: 2;
    }
    &__precio{
        font-size: 6rem;
        font-weight: v.$black;
        color: v.$azul;
        margin: 3rem 0 0 0;
        text-align: center;
        margin: 3rem 0;
    }
}